import { Action, Selection } from '@ali/4ever-cangjie';

export const COLOR_SET = Symbol('COLOR_SET');
export const COLOR_SET_AT_RANGE = Symbol('COLOR_SET_AT_RANGE');

interface SetColorAction {
  type: typeof COLOR_SET;
  payload: {
    color: string;
  }
}

interface SetColorAtRangeAction {
  type: typeof COLOR_SET_AT_RANGE;
  payload: {
    color: string;
    range: Selection;
  }
}

export function setColor(color: string) {
  return {
    type: COLOR_SET,
    payload: {
      color,
    },
  };
}

export function setColorAtRange(range: Selection, color: string) {
  return {
    type: COLOR_SET_AT_RANGE,
    payload: {
      range,
      color,
    }
  }
}

export function isSetColorAction(action: Action): action is SetColorAction {
  return action.type === COLOR_SET;
}

export function isSetColorAtRangeAction(action: Action): action is SetColorAtRangeAction {
  return action.type === COLOR_SET_AT_RANGE;
}

export const actions = {
  setColor,
  setColorAtRange,
}

export const isActions = {
  isSetColorAction,
  isSetColorAtRangeAction,
}
